@import "../../../style/theme/default/var.css";
/*:root{
  !*-----fontsize------*!
  --icon-font-size: 45px;
  !*-----length--------*!
  --padding-col-top: 8px;
  --padding-col-left: 7px;
  --title-height-default: 30px;
  --icon-border-size: 70px;
  !*-----color---------*!
}*/
@component-namespace constantly{
  @b content{
    padding: var(--tab-padding-middle);
    background-color: var(--theme-background-color-primary);
  }
  @b form{
    margin-bottom: var(--theme-margin-base);

    @e platform{
      @utils-clearfix;
      & .el-badge {
        margin-right:var(--theme-margin-small);
        float:left;
      }

      & .el-badge > span{
          display: inline-block;
          text-align: center;
          cursor: pointer;
          width:30px;
          height:30px;
          border-radius: 100%;
          font-size: var(--theme-font-size-small);
          color: var(--color-white);
          background-color: var(--theme-background-color-secondary);
          @when active{
            background-color: var(--theme-color-primary);
          }
      }
      .el-badge__content{
        z-index: 1;
        padding:0 4px;

      }

    }
    .spacing-button{
      line-height: 20px;
      display:inline-block;
    }

  }

@b grid {
  /*margin-left: -var(--theme-margin-middle);*/
  .el-col{
    /*padding-left: var(--theme-padding-middle);*/
    margin-bottom: var(--theme-margin-middle);
  }
  @e content{
    background-color: var(--color-white);
  }
}


  @b widget{
    &.xe-widget.xe-counter {
      margin-bottom: 0;
     }

    &.xe-widget.xe-counter .xe-icon {
       padding: 25px;
       background-color: var(--theme-color-primary);

      & i {
          font-size:44px;
          background-color: transparent;
      }
    }

   &.xe-widget.xe-counter .xe-icon + .xe-label {
     padding-left: var(--theme-padding-middle);
    }
    &.xe-widget.xe-counter .xe-label{
      &  .num ,&  .normal-span{
         font-weight: bold;
        color: var(--theme-font-color-primary);
       }
      &  .num  b{
         font-size: var(--theme-font-size-middle);
       }
      & .cut{
          b{
            font-weight: normal;
            padding-left: 4px;
            padding-right: 4px;
            border-left:1px solid var(--theme-font-color-secondary);
            cursor: pointer;
          @when active{
            font-weight:bold;
          }
          &:first-child {
             border-left:0;
             padding-left:0;
           }
          &:last-child {
             padding-right:0;
           }
        }
      }
         span {
           margin-top: var(--theme-margin-small);
           font-size: var(--theme-font-size-small);
           &.normal-span {
              display: inline-block;
              font-size: inherit;
            }
         }
         i {
           cursor: pointer;
           color: var(--theme-background-color-secondary);
         }
         & .constantly-widget-tips{
           display: inline-block;
           margin-top:0;
         }
    }


    @when unfill{
      &.xe-widget.xe-counter {
        margin-bottom:0;
      }
      &.xe-widget.xe-counter .xe-icon {
        padding: 10px;
        background-color: transparent;
      & i {
            color: var(--theme-color-primary);
            width: 68px;
            height: 68px;
            line-height: 64px;
            border: 2px solid var(--theme-color-primary);
       }
      }

&.xe-widget.xe-counter .xe-icon + .xe-label {
   padding-left: 0;
 }
    }

  }

 @b flow{
   list-style-type: none;
   padding:var(--theme-padding-base) 0;
   margin:0 var(--theme-margin-base);
   border-top:1px solid var(--theme-border-color-primary);

   @e item {
     padding: var(--theme-padding-small);
     line-height: 20px;
     font-size: var(--theme-font-size-small);
     &:hover{
      background-color: var(--theme-background-color-primary);
     border-radius: 3px;
     }
     & .name{
        max-width:60%;
        @utils-ellipsis;
         height:20px;
         line-height:18px;
         display: inline-block;
         vertical-align: middle;
       }
     & i {
        color: var(--theme-background-color-secondary);
         cursor: pointer;
       }
     & .percent{
         background-color: var(--theme-background-color-secondary);
         padding: 2px;
         font-weight: normal;
         color: var(--color-white);
         font-size:var(--theme-font-size-small);
     }


   & .tips{
       display: inline-block;
       vertical-align: middle;
     }
   }

 }


 @b panel {
   background-color: var(--color-white);
   /*padding-bottom: var(--theme-padding-middle);*/
   @e title{
     height:30px;
     line-height: 30px;
     & > span{
       display: inline-block;
       padding:0 var(--theme-padding-middle);
       border-top: 2px solid var(--theme-color-primary)
     }
   }
   @e body {
     .custom{
       list-style-type: none;
       padding: 0 5px 15px;
       margin-right:var(--theme-padding-base);
       margin-bottom:0;
       @utils-clearfix;
       & > li {
        width:20%;
        padding-left: var(--theme-padding-base);
        margin-top:var(--theme-margin-base);
        float:left;

         .custom-widget {
           background-color: var(--theme-background-color-primary);
           position: relative;
           transform-style: preserve-3d;
           transition: transform 1s;
           -ms-transform-style: inherit;
           &:hover{
              transform: rotateY(180deg);
              -ms-transform: rotateY(0deg);
              & .custom-more{
                  display:block;
                 _display:block;
                 -ms-display:block;
              }
            }
         }
         .custom-total {
           -webkit-backface-visibility: hidden;
           -moz-backface-visibility: hidden;
           backface-visibility: hidden;
           line-height: 1;
           display: table;
           width:100%;
         }
         .custom-percent {
           width: 1%;
           display: table-cell;
           vertical-align: middle;
          padding: var(--theme-padding-middle);
          & span{
              width: 64px;
              height: 64px;
              display: block;
              line-height: 64px;
              text-align: center;
              color: var(--theme-font-color-secondary);
              font-size: 26px;
              b {
                font-weight: normal;
                width:100%;
                height:100%;
                border: 1px solid var(--theme-border-color-primary);
                border-radius: 100%;
                display: block;
              }
              i {
                font-size:14px;
                font-style:normal;
              }
            }
          }
         .custom-des{
           text-align: right;
           display: table-cell;
           vertical-align: middle;
           padding: var(--theme-padding-middle) var(--theme-padding-middle) var(--theme-padding-middle) 0;
           & .num,
           & .num .normal-span{
             color: var(--theme-color-primary);
             font-size: 26px;
              display: block;
              font-weight: normal;
           }
           & span{
               margin-top: var(--theme-margin-small);
               display: block;
           }
         }
         .custom-more {
           position: absolute;
           top:0;
           left:0;
           bottom:0;
           right:0;
           width:100%;
           height: 100%;
           transform: rotateY(180deg);
           -ms-transform: rotateY(0deg);
           text-align: center;
           line-height:94px;
           background-color:var(--theme-color-primary);
           color: var(--color-white);
           _display:none;
           -ms-display:none;
           -webkit-backface-visibility: hidden;
           -moz-backface-visibility: hidden;
           backface-visibility: hidden;
           & a{
              color: var(--color-white);
              cursor: pointer;
           }
         }
       }
     }

   }
 }

 @b salestrend{
   padding: var(--theme-padding-base);

   .order-addbtn.el-button{
       height:30px;
       vertical-align: middle;
       background-color: var(--theme-background-color-secondary);
       border-color: var(--theme-background-color-secondary);
       color: var(--color-white);
       padding-left: var(--theme-padding-base);
       padding-right: var(--theme-padding-base);
       &:focus,
       &:hover{
          border-color: var(--theme-background-color-secondary);
        }
     }
     @e order {
       .el-radio-button{
         margin-right: var(--theme-margin-base);
       }
       .el-radio-button .el-radio-button__inner{
         border-radius: var(--border-radius-small);
         border-left: 1px solid var(--theme-border-color-primary);
         background-color: var(--theme-background-color-secondary);
         border-color:var(--theme-background-color-secondary);
         color: var(--color-white);
         padding:8px 10px;
     }
       .el-radio-button__orig-radio:checked + .el-radio-button__inner{
         border-left: 1px solid var(--theme-color-primary);
         background-color: var(--theme-color-primary);
         border-color:var(--theme-color-primary);
       }
     .el-radio-button:first-child .el-radio-button__inner {
       border-radius: var(--border-radius-small);
     }
       .el-radio-button:last-child .el-radio-button__inner {
         border-radius: var(--border-radius-small);
       }
     }
   }


  @b echarts{
    & .echarts{
      width:100%;
    }
    & .oscillogram{
      height:400px;
    }
    & .funnel{
        height:223px;
      }
  }

}

.order-popover{
.el-checkbox{
  width:100%;
  height: 18px;
  @utils-ellipsis;
}
  .el-checkbox:first-child{
    margin-left: var(--theme-margin-base);
  }
  .el-checkbox + .el-checkbox{
    margin-left: var(--theme-margin-base);
  }
  .order-popover-footer{
    padding-top:5px;
    text-align: right;
  }
  .el-checkbox__label {
    font-size: var(--theme-font-size-small);
  }
}


@media (max-width: 1680px){
  .constantly-panel__body .custom > li .custom-percent {
    padding:10px;
  }
  .constantly-panel__body .custom > li .custom-des {
    padding:10px 10px 10px 0;
  }
}
@media (max-width: 1620px){
  .constantly-widget.xe-widget.xe-counter .xe-label span {
    font-size:12px;
  }
  .constantly-widget.xe-widget.xe-counter .xe-icon + .xe-label{
    padding: var(--theme-padding-base);
  }
  .constantly-widget.xe-widget.xe-counter .xe-icon {
    padding: var(--theme-padding-middle);
  }
  .constantly-flow__item .name {
    max-width: 48%;
  }

  .constantly-echarts .funnel {
    height:203px;
  }
}
@media(max-width: 1520px) {
  .constantly-panel__body .custom > li .custom-percent {
    padding:10px;
  }
  .constantly-panel__body .custom > li .custom-percent span{
    width: 54px;
    height: 54px;
    line-height: 54px;
    font-size:24px;
  }
  .constantly-panel__body .custom > li .custom-des {
    padding:10px 10px 10px 0;
  },
  .constantly-panel__body .custom > li .custom-des .num,
  .constantly-panel__body .custom > li .custom-des .num .normal-span {
    font-size:20px;
  }
  .constantly-panel__body .custom > li .custom-des span{
    font-size:12px;
  }
  .constantly-panel__body .custom > li .custom-more {
    line-height: 74px;
  }

  .constantly-echarts .funnel {
    height:183px;
  }

}
@media (max-width: 1370px) {
  .constantly-widget.xe-widget.xe-counter .xe-icon + .xe-label{
    padding: 5px;
  }
  .constantly-widget.xe-widget.xe-counter .xe-label span{
    font-size: var(--theme-font-size-small);
  }
 .constantly-widget.xe-widget.xe-counter .xe-label  .num,
 .constantly-widget.xe-widget.xe-counter .xe-label  .num .normal-span{
    font-size:22px;
  }
  .constantly-widget.xe-widget.xe-counter .xe-icon {
    padding: 10px;
    & i{
        font-size: 30px;
        line-height: 44px;
        width: 44px;
        height: 44px;
      }
  }
.constantly-widget.is-unfill.xe-widget.xe-counter .xe-icon i{
  line-height: 44px;
  width: 48px;
  height: 48px;
}
  .constantly-flow__item .name {
    max-width: 42%;
  }

  .constantly-panel__body .custom > li .custom-percent {
    padding:10px 5px;
  }
  .constantly-panel__body .custom > li .custom-percent span{
    width: 44px;
    height: 44px;
    line-height: 44px;
    font-size:20px;
    & i {
    font-size: 12px;
      }
  }
  .constantly-panel__body .custom > li .custom-des {
    padding:5px 5px 5px 0;
  }
  .constantly-panel__body .custom > li .custom-des .num,
  .constantly-panel__body .custom > li .custom-des .num .normal-span{
    font-size:18px;
  }
  .constantly-panel__body .custom > li .custom-des span{
    font-size:12px;
  }
.constantly-panel__body .custom > li .custom-more {
  line-height: 54px;
}

.constantly-echarts .funnel {
  height:163px;
}
}



